export default "@import url('https://fonts.googleapis.com/css?family=Space+Mono:400,400i,700,700i');\n\n.enrollment{\n    height:100%;\n    padding: 20px;\n    background: #ecdfae;\n}\n\n.branch-selector{\n        text-align: center;\n        display: block;\n        height: 100%;\n        padding: 20px;\n}\n\n.display-branch-details{\n    display: grid;\n    grid-template-columns: auto auto;\n    margin: 0 auto;\n    width: 35%;\n    padding: 20px;\n}\n\n.form-large-text{\n    font-weight: 100;\n    font-size: large;\n}\n\n.form-text-color{\n    color: #1520b8 !important;\n}\n\n.customer-details{\n    margin: 0 auto;\n\n}\n\n.loadingimage{\n    margin: 0 auto;\n    width: 100%;\n    text-align: center;\n    height: calc(100vh - 100px);\n    background-color: black;\n}\n\n.statusMsg{\n    color: white;\n}\n\n.text-align-left{\n    text-align: left;\n}\n\n.form-button{\n    color: #fff;\n    background-color: rgb(25, 101, 252);\n    border-color: rgb(25, 101, 252);\n    border-radius: .25rem;\n    padding: .075rem .75rem;\n}\n\n.form-button:hover {\n    background-color: #39ace7;\n}\n\n.title {\n    margin-bottom: 30px;\n    color: #162969;\n    margin: 0 auto;\n    width: 21%;\n    margin-top:5px;\n    text-align: center;\n}\n\n.card{\nwidth: 320px;\nheight: 190px;\n  -webkit-perspective: 600px;\n  perspective:600px;\n  margin: 0 auto;\n  margin-top: 88px;\n  \n}\n\ncard-display{\n    margin:0 auto;\n}\n\n.card__part{\n  box-shadow: 1px 1px #aaa3a3;\n    top: 0;\n  position: absolute;\nz-index: 1000;\n  left: 0;\n  display: inline-block;\n    width: 320px;\n    height: 190px;\n    background-image: url('https://image.ibb.co/bVnMrc/g3095.png'), -webkit-gradient(linear, left top, right bottom, from(#fd696b), color-stop(#fa616e), color-stop(#f65871), color-stop(#f15075), to(#ec4879));\n    background-image: url('https://image.ibb.co/bVnMrc/g3095.png'), linear-gradient(to right bottom, #fd696b, #fa616e, #f65871, #f15075, #ec4879); /*linear-gradient(to right bottom, #fd8369, #fc7870, #f96e78, #f56581, #ee5d8a)*/\n    background-repeat: no-repeat;\n    background-position: center;\n    background-size: cover;\n    border-radius: 8px;\n   \n    -webkit-transition: all .5s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n    transition: all .5s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n    -webkit-transform-style: preserve-3d;\n    -moz-transform-style: preserve-3d;\n    -webkit-backface-visibility: hidden;\n    -moz-backface-visibility: hidden;\n}\n\n.card__front{\n  padding: 18px;\n-webkit-transform: rotateY(0);\n-moz-transform: rotateY(0);\n}\n\n.card__back {\n  padding: 18px 0;\n-webkit-transform: rotateY(-180deg);\n-moz-transform: rotateY(-180deg);\n}\n\n.card__black-line {\n    margin-top: 5px;\n    height: 38px;\n    background-color: #303030;\n}\n\n.card__logo {\n    height: 16px;\n}\n\n.card__front-logo{\n      position: absolute;\n    top: 18px;\n    right: 18px;\n}\n\n.card__square {\n    border-radius: 5px;\n    height: 30px;\n}\n\n.card_numer {\n    display: block;\n    width: 100%;\n    word-spacing: 4px;\n    font-size: 20px;\n    letter-spacing: 2px;\n    color: #fff;\n    text-align: center;\n    margin-bottom: 20px;\n    margin-top: 20px;\n}\n\n.card__space-75 {\n    width: 75%;\n    float: left;\n}\n\n.card__space-25 {\n    width: 25%;\n    float: left;\n}\n\n.card__label {\n    font-size: 10px;\n    text-transform: uppercase;\n    color: rgba(255,255,255,0.8);\n    letter-spacing: 1px;\n}\n\n.card__info {\n    margin-bottom: 0;\n    margin-top: 5px;\n    font-size: 16px;\n    line-height: 18px;\n    color: #fff;\n  letter-spacing: 1px;\n  text-transform: uppercase;\n}\n\n.card__back-content {\n    padding: 15px 15px 0;\n}\n\n.card__secret--last {\n    color: #303030;\n    text-align: right;\n    margin: 0;\n    font-size: 14px;\n}\n\n.card__secret {\n    padding: 5px 12px;\n    background-color: #fff;\n    position:relative;\n}\n\n.card__secret:before{\n  content:'';\n  position: absolute;\ntop: -3px;\nleft: -3px;\nheight: calc(100% + 6px);\nwidth: calc(100% - 42px);\nborder-radius: 4px;\n  background: repeating-linear-gradient(45deg, #ededed, #ededed 5px, #f9f9f9 5px, #f9f9f9 10px);\n}\n\n.card__back-logo {\n    position: absolute;\n    bottom: 15px;\n    right: 15px;\n}\n\n.card__back-square {\n    position: absolute;\n    bottom: 15px;\n    left: 15px;\n}\n\n.card:hover .card__front {\n    -webkit-transform: rotateY(180deg);\n    -moz-transform: rotateY(180deg);\n\n}\n\n.card:hover .card__back {\n    -webkit-transform: rotateY(0deg);\n    -moz-transform: rotateY(0deg);\n}\n\n.qrcode{\n    margin: 0 auto;\n    text-align: center;\n}\n\n#qrcodeinsert{\n    background-image: url(\"https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=Example\");\n}\n\n.qr{\n    text-align: center;\n}\n\nh4{\n    margin: 0 auto;\n    text-align: center;\n}\n\np-messages{\n    position: fixed;\n    width: 100%;\n    bottom: 0;\n}\n\n#dropdownsubmit{\n    margin: 0 auto;\n    margin-top: 21px;\n    /* top: 26px; */\n    display: block;\n}\n\n#card{\n    display: block;\n}\n\n#cardloading{\n    width: 520px;\n    height: 520px;\n}\n/*# sourceMappingURL=data:application/json;base64, */"